import { Link, useParams } from "react-router-dom"
import FormGroup from "../../Components/FormGroup"
import ButtonBack from "../Components/Buttons/ButtonBack"
import PageContent from "../Components/PageContent"
import PageTitle from "../Components/PageTitle"
import { useCallback, useEffect, useState } from "react"
import { destroy, get } from "../../Functions/Api"
import Table from "../Components/Table"
import ButtonEdit from "../Components/Buttons/ButtonEdit"
import ButtonDelete from "../Components/Buttons/ButtonDelete"
import Card from "../../Components/Card"
import { ConfirmDelete, PopupAlert } from "../../Functions/Alerts"

const DetailKK = () => {
    const [detailKeluarga, setDetailKeluarga] = useState<DataT>()
    const { no_kk, type } = useParams()

    const getDetailKeluarga = useCallback(async () => {
        const res = await get(`data-keluarga/${no_kk}`)
        if (res.ok) setDetailKeluarga(res.data)
        return res
    }, [no_kk])

    useEffect(() => {
        getDetailKeluarga()
    }, [getDetailKeluarga])

    const deleteAnggotaKeluarga = (nik: string) => {
        ConfirmDelete(async () => {
            const res = await destroy(`anggota-keluarga/${nik}?no_kk=${no_kk}`)
            if (!res.ok) return

            const newDetailKeluarga = await getDetailKeluarga()

            if (newDetailKeluarga.ok) {
                PopupAlert.fire({
                    icon: "success",
                    text: res.data.message
                })
            }
        })
    }

    const RowHead = (
        <tr>
            <th>No</th>
            <th>Nama</th>
            <th>NIK</th>
            <th colSpan={2}>ACTION</th>
        </tr>
    )
    return (
        <>
            <PageTitle title="Data Keluarga" />
            <ButtonBack />
            <PageContent>
                <Card
                    cardHeader={
                        <h4 className="card-title text-success">
                            Detail Anggota Keluarga
                        </h4>
                    }
                    cardClassName="p-3"
                    cardBody={
                        <form>
                            <div className="row">
                                {detailKeluarga && (
                                    <>
                                        <div className="col-md-6">
                                            <FormGroup
                                                label="Nomor KK"
                                                type="text"
                                                id="no-kk"
                                                defaultValue={
                                                    detailKeluarga.no_kk
                                                }
                                                disabled={true}
                                            />
                                            <FormGroup
                                                label="Nama Kepala Keluarga"
                                                type="text"
                                                id="nama-kepala-keluarga"
                                                defaultValue={
                                                    detailKeluarga
                                                        .kepala_keluarga.nama
                                                }
                                                disabled={true}
                                            />
                                            <FormGroup
                                                label="NIK Kepala Keluarga"
                                                type="text"
                                                id="nik-kepala-keluarga"
                                                defaultValue={
                                                    detailKeluarga
                                                        .kepala_keluarga.nik
                                                }
                                                disabled={true}
                                            />
                                            <FormGroup
                                                label="Nomor Whatsapp"
                                                type="text"
                                                id="no-wa"
                                                defaultValue={
                                                    detailKeluarga
                                                        .kepala_keluarga
                                                        .no_whatsapp
                                                }
                                                disabled={true}
                                            />
                                        </div>
                                        <div className="col-md-6">
                                            <div className="row align-items-center">
                                                <p className="mb-0">Foto KK</p>
                                                <p className="col my-auto text-success">
                                                    Foto KK
                                                </p>
                                                <Link
                                                    className="col-md-4 btn btn-success btn-lg fs-6"
                                                    to={`/${type}/data-keluarga/${no_kk}/foto-kk/`}
                                                    rel="noreferrer"
                                                    target="_blank"
                                                >
                                                    Lihat Foto
                                                </Link>
                                            </div>
                                        </div>
                                    </>
                                )}
                            </div>
                        </form>
                    }
                />
                <Table
                    title="Tabel Data Keluarga"
                    rowThead={RowHead}
                    search={{
                        url: "",
                        dispatch: setDetailKeluarga
                    }}
                >
                    {detailKeluarga &&
                        (detailKeluarga.anggota_keluarga as DataT[]).map(
                            ({ nama, nik }, i) => (
                                <tr key={i}>
                                    <td className="text-bold-500">{++i}</td>
                                    <td>{nama}</td>
                                    <td>{nik}</td>
                                    <td className="d-flex justify-content-center align-items-center gap-2">
                                        <ButtonEdit
                                            to={`/${type}/data-keluarga/${no_kk}/${nik}`}
                                        />
                                        <ButtonDelete
                                            props={{
                                                onClick: () => {
                                                    deleteAnggotaKeluarga(nik)
                                                }
                                            }}
                                        />
                                    </td>
                                </tr>
                            )
                        )}
                </Table>
            </PageContent>
        </>
    )
}

export default DetailKK
